// --- Componente: NotificationBell (Novo) ---
function NotificationBell({ manicureId }: { manicureId: string }) {
    const [notifications, setNotifications] = useState<NotificationItem[]>([]);
    const [isOpen, setIsOpen] = useState(false);
    const unreadCount = notifications.filter(n => !n.is_read).length;

    const fetchNotifications = useCallback(async () => {
        if (!manicureId) return;

        const { data, error } = await supabase
            .from('notifications')
            .select('*')
            .eq('manicure_id', manicureId)
            .order('created_at', { ascending: false })
            .limit(10); // Limita a 10 para não sobrecarregar

        if (!error) {
            setNotifications(data as NotificationItem[]);
        }
    }, [manicureId]);

    // Lógica de Realtime para Notificações
    useEffect(() => {
        fetchNotifications();

        const channel = supabase
            .channel(`manicure_notifications_${manicureId}`)
            .on(
                'postgres_changes',
                { event: 'INSERT', schema: 'public', table: 'notifications', filter: `manicure_id=eq.${manicureId}` },
                () => {
                    fetchNotifications(); // Re-fetch na inserção
                }
            )
            .subscribe();

        return () => { supabase.removeChannel(channel); };
    }, [manicureId, fetchNotifications]);

    const handleMarkAsRead = async () => {
        const idsToMark = notifications.filter(n => !n.is_read).map(n => n.id);
        if (idsToMark.length === 0) return;

        await supabase
            .from('notifications')
            .update({ is_read: true })
            .in('id', idsToMark);
            
        // Atualiza o estado local para refletir a leitura
        setNotifications(notifications.map(n => ({...n, is_read: true})));
    };
    
    // Função para formatar o tempo de criação
    const formatTime = (isoString: string) => {
        const diff = new Date().getTime() - new Date(isoString).getTime();
        const minutes = Math.floor(diff / 60000);
        if (minutes < 1) return "Agora";
        if (minutes < 60) return `${minutes} min atrás`;
        const hours = Math.floor(minutes / 60);
        if (hours < 24) return `${hours} h atrás`;
        return new Date(isoString).toLocaleDateString('pt-BR');
    };


    return (
        <div className="relative">
            <button 
                onClick={() => { setIsOpen(!isOpen); if (isOpen) handleMarkAsRead(); }} 
                className="text-gray-400 hover:text-gray-500 rounded-full p-1 relative"
            >
                <Bell className="w-6 h-6" />
                {unreadCount > 0 && (
                    <span className="absolute top-0 right-0 block h-2 w-2 rounded-full bg-mani-pink-500 ring-2 ring-white animate-pulse" />
                )}
            </button>

            {isOpen && (
                <div 
                    className="absolute right-0 mt-2 w-80 bg-white rounded-lg shadow-xl py-2 ring-1 ring-black ring-opacity-5 z-50 transform translate-x-2"
                    onMouseLeave={() => { setIsOpen(false); handleMarkAsRead(); }}
                >
                    <div className="px-4 py-2 text-lg font-semibold text-gray-800 border-b">Notificações</div>
                    
                    {notifications.length === 0 ? (
                        <p className="p-4 text-center text-gray-500 text-sm">Nenhuma notificação recente.</p>
                    ) : (
                        <ul className="max-h-80 overflow-y-auto">
                            {notifications.map(n => (
                                <li 
                                    key={n.id} 
                                    className={`px-4 py-3 border-b text-sm transition ${!n.is_read ? 'bg-mani-pink-50' : 'hover:bg-gray-50'}`}
                                >
                                    <p className={`font-medium ${!n.is_read ? 'text-gray-900' : 'text-gray-700'}`}>
                                        {n.message}
                                    </p>
                                    <p className="text-xs text-gray-400 mt-1">
                                        {formatTime(n.created_at)}
                                    </p>
                                </li>
                            ))}
                        </ul>
                    )}
                    
                </div>
            )}
        </div>
    );
}

// --- Componente: Header (Ajustado para usar o Bell Component) ---
function Header({ user }: { user: { name: string, avatarUrl: string } }) {
  const router = useRouter();
  const [menuOpen, setMenuOpen] = useState(false);
  const [manicureName, setManicureName] = useState(user.name);
  const [manicureId, setManicureId] = useState<string | null>(null); // NOVO: Captura o ID para passar para o Bell

  const handleSignOut = async () => {
    const { error } = await supabase.auth.signOut();
    if (!error) {
      router.replace('/'); 
    }
  };

  useEffect(() => {
    async function loadProfile() {
      const { data: { user: authUser } } = await supabase.auth.getUser();
      if (!authUser) return;
      setManicureId(authUser.id); // Captura o ID aqui!

      const { data: profile } = await supabase
        .from('profiles')
        .select('name')
        .eq('id', authUser.id)
        .single();
      
      if (profile) {
        setManicureName(profile.name);
      }
    }
    loadProfile();
  }, []);
  
  const isActive = (path: string) => typeof window !== 'undefined' && window.location.pathname === path;


  return (
    <header className="bg-white shadow-sm border-b border-gray-200 sticky top-0 z-10">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          <div className="flex items-center space-x-8">
            <h1 className="text-2xl font-bold text-mani-pink-600">ManiHelp</h1>
            <nav className="hidden md:flex space-x-6">
              <a href="/dashboard" 
                 className={`font-medium px-1 py-2 text-sm transition ${isActive('/dashboard') ? 'text-mani-pink-600 border-b-2 border-mani-pink-600' : 'text-gray-500 hover:text-gray-700'}`}>
                <Calendar className="inline-block w-5 h-5 mr-1" />
                Agenda
              </a>
              <a href="/clients" 
                 className={`font-medium px-1 py-2 text-sm transition ${isActive('/clients') ? 'text-mani-pink-600 border-b-2 border-mani-pink-600' : 'text-gray-500 hover:text-gray-700'}`}>
                <Users className="inline-block w-5 h-5 mr-1" />
                Clientes
              </a>
              <a href="/schedules" 
                 className={`font-medium px-1 py-2 text-sm transition ${isActive('/schedules') ? 'text-mani-pink-600 border-b-2 border-mani-pink-600' : 'text-gray-500 hover:text-gray-700'}`}>
                <Clock className="inline-block w-5 h-5 mr-1" />
                Horários
              </a>
              <a href="#" 
                 className={`font-medium px-1 py-2 text-sm transition text-gray-400 cursor-not-allowed`}
                 title="Recurso em desenvolvimento"
              >
                <BarChart3 className="inline-block w-5 h-5 mr-1" />
                <Lock className="inline-block w-4 h-4 mr-1" /> 
                Relatórios
              </a>
            </nav>
          </div>

          <div className="flex items-center space-x-4">
            {/* COMPONENTE BELL ATIVO */}
            {manicureId && <NotificationBell manicureId={manicureId} />} 

            <div className="relative">
              <button onClick={() => setMenuOpen(!menuOpen)} className="flex items-center space-x-2 rounded-full p-1 pr-2 hover:bg-gray-100">
                <img 
                  className="w-8 h-8 rounded-full object-cover" 
                  src={user.avatarUrl}
                  alt="Avatar do usuário" 
                  onError={(e) => { e.currentTarget.onerror = null; e.currentTarget.src="https://placehold.co/100x100/E62E7A/FFFFFF?text=M"; }}
                />
                <span className="hidden sm:inline font-medium text-sm text-gray-700">{manicureName}</span>
                <ChevronDown className={`w-4 h-4 text-gray-500 transition-transform ${menuOpen ? 'rotate-180' : ''}`} />
              </button>

              {menuOpen && (
                <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-xl py-1 ring-1 ring-black ring-opacity-5 z-20">
                  <a href="#" className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100">
                    <Settings className="inline-block w-4 h-4 mr-2" />
                    Configurações
                  </a>
                  <button onClick={handleSignOut} className="w-full text-left block px-4 py-2 text-sm text-red-600 hover:bg-red-50">
                    <LogOut className="inline-block w-4 h-4 mr-2" />
                    Sair
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </header>
  );
}


